import React from 'react'

const Option = (props) => (
  <div className='option'>
    <p className='option__text'>{props.count}. {props.opText}</p>
    
    <button
    className='button button--link'
      onClick={(e) => {
        props.handleDeleteOption(props.opText);
      }}>
      Remove
          </button>
  </div>
)

export default Option
